/**
 *  Vue
 */
export default {
  props: {
    // 
    position: Array,
    // 
    zIndex: Number,
    // 
    fill: String
  },
  watch: {
    position: {
      deep: true,
      handler() {
        //  utils/feature.js 
        this.update()
      }
    }
  },
  computed: {
    coordinate() {
      // 
      return this.position.map(n => Number.parseFloat(n))
    }
  },
  methods: {
    baseStyleRender() {
      return {
        className: 'Style',
        zIndex: this.zIndex,
        fill: this.fill
      }
    }
  }
}
