/**
 * 
 */
export default {
  props: {
    text: String,
    color: String,
    font: String,
    background: {
      type: String,
      default: 'transparent'
    }
  },
  computed: {
    fontWithFamily() {
      return `${this.font} sans-serif`
    }
  },
  watch: {
    /**
     * 
     */
    text() {
      this.setStyle()
    }
  },
  methods: {
    textRender() {
      return {
        className: 'Text',
        text: this.text,
        font: this.fontWithFamily,
        fill: {
          className: 'Fill',
          color: this.color
        },
        backgroundFill: {
          className: 'Fill',
          color: this.background
        }
      }
    }
  }
}
